* {
    font-family: 'Poppins', sans-serif;
}

.weves {
    background-image: url(/assets/weves.png);
    background-size: cover;
}

.text-style {
    color: #8f57d8;
}

.fig-text {
    font-size: 25px;
    margin-top: 30px;
}

.box-style {
    background: linear-gradient(70deg, #8f57d8 33%, #0d61bc 66%, #1d98ff);
    color: white;
    border: none;
}

.btn-rainbow {
    width: 100%;
    height: 70px;
    font-size: 25px;
    margin-bottom: 40px !important;
}

.back-grey {
    background-color: rgb(236, 235, 235);
}

@media screen and (max-width: 800px) {
    .weves {
        height: 1500px !important;
    }
    .phone-1 {
        height: 520px;
        width: 300px;
        margin: auto;
    }
    .mar-botm {
        margin-bottom: 50px;
    }
    .mar {
        margin-left: -50px !important;
    }
    .pad-l {
        padding-left: 10% !important;
    }
    .width-med {
        width: 80% !important;
    }
    .fnt-med {
        font-size: 18px !important;
    }
    .form-med {
        margin-left: 30% !important;
    }
    .btn-med {
        width: 220px !important;
        margin-right: 30% !important;
    }
    .fnt-med-2 {
        font-size: 24px !important;
    }
    .fnt-med-3 {
        font-size: 34px !important;
    }
    .tel-img {
        width: 20px;
        height: 20px;
    }
    .cont-img {
        width: 140px;
        height: 80px;
    }
    .info-cont {
        font-size: 12px !important;
    }
}